import React, {Component} from 'react';
import './App.css';
import data from './LI_posts.json'

function Hiring(){
    const hiring_container_styles = {
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        flexWrap: "wrap",
        backgroundColor: "#0E2439"
    }
    const tile_style = {
        margin: "20px",
        border: "solid light-gray 1px",
        opacity: "0.9"
    }
    return (
        <div id={"hiring"} style={hiring_container_styles}>
            {data.hiring.map(d => (
                <div style={tile_style} dangerouslySetInnerHTML={{__html: d.html}}/>
            ))}
        </div>
    )
}
function Looking(){
    const looking_container_styles = {
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        flexWrap: "wrap",
        backgroundColor: "#0E2439"
    }
    const tile_style = {
        margin: "20px",
        border: "solid light-gray 1px"
    }
    return (
        <div id={"looking"} style={looking_container_styles}>
            {data.looking.map(d => (
                <div style={tile_style} dangerouslySetInnerHTML={{__html: d.html}}/>
            ))}
        </div>
    )
}

class App extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            buttonId: null
        }
        this.setButton = this.setButton.bind(this);
    }
    setButton(id){
        this.setState({buttonId: id});
    }
    render() {
        const header_container_styles = {
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            borderBottom: ".15em solid #1F364D",
            padding: ".5em",
            backgroundColor: "#1F364D",
            color: "#9CB3C9",
            position: ""
        }
        const footer_container_styles = {
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-evenly",
            alignItems: "center",
            borderTop: ".15em solid #1F364D",
            padding: "1.5em",
            // margin: ".5em",
            backgroundColor: "#1F364D",
            color: "#9CB3C9",
            fontWeight: "bold"
        }
        const header_styles = {
            padding: ".5em"
        }
        const subheader_styles = {
            fontStyle: "italic",
            color: "#9CB3C9"
        }
        const button_styles = {
            padding: ".5em",
            margin: "10px",
            backgroundColor: "#4899EB",
            border: "0",
            borderRadius: "5px",
            color: "white",
            fontWeight: "bold"
        }
        const top_banner_styles = {
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            borderTop: ".15em solid gray",
            borderBottom: ".15em solid gray",
            padding: "1em",
            backgroundColor: "#1F364D",
            color: "#9CB3C9",
            // fontWeight: "bold"
        }
        const bottom_banner_styles = {
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            borderBottom: ".15em solid gray",
            padding: "1em",
            backgroundColor: "#1F364D",
            color: "#9CB3C9",
            // fontWeight: "bold"
        }
        const buy_me_coffee_button_styles = {
            height: "51px",
            width: "217px",
        }
        return (
            <div>
                <div style={header_container_styles}>
                    <h1 style={header_styles}>{"LeadGen.io (beta release)"}</h1>
                    {/*<h4 style={subheader_styles}>The World's First Lead Generation Marketplace</h4>*/}
                    <h4 style={subheader_styles}>New leads every day</h4>
                    <div>
                        <input style={button_styles} className={this.state.buttonId === 1? "button1 orange" :
                            "button1"} onClick={() => this.setButton(1)} value="I'm a Job Seeker looking for roles"
                               type="button" ref="button" />
                        <input style={button_styles} className={this.state.buttonId === 2? "button2 orange" :
                            "button2"} onClick={() => this.setButton(2)}  value="I'm a Recruiter looking for leads"
                               ref="button1" type="button" />
                    </div>
                </div>
                <div style={top_banner_styles}>
                    <h4>How to cold call</h4>
                    <a href={"https://www.themuse.com/advice/how-a-simple-linkedin-message-that-took-2-minutes-to-write-landed-me-my-dream-job"}>How a Simple Linkedin Message That Took 2 Minutes to Write Landed Me My Dream Job</a>
                    <a href={"https://www.themuse.com/advice/how-to-write-linkedin-messages-that-actually-get-read"}>How To Write Linkedin Messages That Actually Get Read</a>
                    <a href={"https://www.inhersight.com/blog/how-to/how-to-reach-out-to-someone-on-linkedin?_n=88838653"}>How To Reach Out To Someone on Linkedin</a>
                </div>
                <div style={bottom_banner_styles}>
                    <h4>Our team wants to talk to users like you about your job search experiences.</h4>
                    <a href={"https://forms.gle/VpM8AYrdbUgocqCh8"}>Sign up here</a>
                </div>

                {this.state.buttonId === 1 && <Hiring/>}
                {this.state.buttonId === 2 && <Looking/>}
                {this.state.buttonId !== 1  && this.state.buttonId !== 2 && <Hiring/>}

                <div>
                    <div style={footer_container_styles}>
                        <h7 style={subheader_styles}> <a href="mailto:optimumz.web@gmail.com">Give us feedback</a></h7>
                        <h7 style={subheader_styles}> Want a 1-on-1 consultation with a software engineer? <a href="https://calendly.com/hshidara/15min"> We got you </a></h7>
                        <a href="https://www.buymeacoffee.com/hideshidara" target="_blank"><
                            img src={"https://cdn.buymeacoffee.com/buttons/default-orange.png"} alt={"Buy Me A Coffee"} style={buy_me_coffee_button_styles}>
                        </img></a>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;
